.hero {
    text-align: center;

    &__video {
        @extend .embed-container;
        box-shadow: unquote(shadow(box-200));
    }

    &__content {
        padding: 3rem 0 6rem;
        color: colour(white);
        @include media(md) {
            padding: 2rem 0 3rem;
        }

        dt {
            @extend h3;
            text-transform: uppercase
        }

        dd {
            @extend h1;
            margin: 0;
        }
    }

    &__links {
        margin-bottom: 3rem;
    }

    &__image {
        position: absolute;
        inset: auto 0 0 0;
        width: 100%;
        z-index: -1;

        img {
            object-fit: contain;
            height: 100%;
            width: 100%;
        }
    }
}
