.faqs {
    display: flex;
    flex-direction: column;
    gap: 2rem;

    .faq {
        &__question {
            position: relative;
            border-bottom: 2px solid colour(pink);
            cursor: pointer;
            padding: .5rem;
            width: 100%;

            &::after {
                position: absolute;
                content: '+';
                inset: 7px 0.5rem auto auto;
                font-size: 2rem;
            }

            h2 {
                @extend h4;
                margin: 0;
                max-width: calc(100% - 1.25rem);
            }
        }

        &__answer {
            display: none;
        }

        &.--is-open {
            .faq__question {
                &::after {
                    content: '-';
                }
            }
            .faq__answer {
                display: block;
                margin-top: 1rem;
            }
        }
    }
}
