/* Variables */

//-----------------------------------------
// Site colours
//-----------------------------------------
$colours: (
    black: #000,
    charcoal: #202226,
    light-grey: #eaeaea,
    white: #fff,
    pink: #e2144f,
    blue-gery: #8eadba,
);

//-----------------------------------------
// Applied Colours
//-----------------------------------------
$plain-text: colour(black);
$meta-text: colour(light-grey);
$link: colour(pink);
$link-hover: darken(colour(pink), 10%);
$border: lighten(colour(pink), 80%);

//-----------------------------------------
// Fonts
//-----------------------------------------
@font-face {
    font-family: 'Whitney';
    font-weight: 900;
    src: url('/assets/fonts/whitneybold.otf') format('opentype');
    font-display: swap;
}

@font-face {
    font-family: 'Whitney';
    font-weight: 700;
    src: url('/assets/fonts/whitneysemibold.otf') format('opentype');
    font-display: swap;
}

@font-face {
    font-family: 'Whitney';
    font-weight: 500;
    src: url('/assets/fonts/whitneymedium.otf') format('opentype');
    font-display: swap;
}

@font-face {
    font-family: 'Whitney';
    font-weight: 300;
    src: url('/assets/fonts/whitneybook.otf') format('opentype');
    font-display: swap;
}

@font-face {
    font-family: 'Whitney';
    font-weight: 100;
    src: url('/assets/fonts/whitneylight.otf') format('opentype');
    font-display: swap;
}

$font-main: 'Whitney', sans-serif;

//-----------------------------------------
// Site shadows
// Must be called using unquote(shadow(value))
//-----------------------------------------
$shadows: (
    box-100: '0px 2px 4px -1px rgba(0, 0, 0, 0.08), 0px 2px 20px -2px rgba(0, 0, 0, 0.04), 0px 7px 33px -3px rgba(0, 0, 0, 0.07)',
    box-200: '0px 3px 4px -1px rgba(0, 0, 0, 0.12), 0px 9px 33px -2px rgba(0, 0, 0, 0.08), 0px 12px 42px -8px rgba(0, 0, 0, 0.12)',
    box-300: '0px 3px 4px rgba(0, 0, 0, 0.12), 0px 9px 33px -1px rgba(0, 0, 0, 0.1), 0px 12px 42px -8px rgba(0, 0, 0, 0.15)',
    box-800: '0px 3px 4px rgba(0, 0, 0, 0.4), 0px 9px 33px -1px rgba(0, 0, 0, 0.44), 0px 12px 42px -8px rgba(0, 0, 0, 0.49)',
    box-900: '0px 3px 4px rgba(0, 0, 0, 0.85), 0px 9px 33px -1px rgba(0, 0, 0, 0.75), 0px 12px 42px -8px rgba(0, 0, 0, 0.66)',
);

//-----------------------------------------
// Media Query Breakpoints
//-----------------------------------------
$breakpoints: (
    xs: 320px,
    sm: 576px,
    md: 800px,
    lg: 1024px,
    xl: 1200px,
);

//-----------------------------------------
// Grid Defaults
//-----------------------------------------
$gutter: 2rem;
