.button {
    display: inline-block;
    background: colour(white);
    border: 2px solid colour(pink);
    color: colour(pink);
    margin: .25rem .25rem .25rem 0;
    padding: .5rem 1.375rem;
    text-transform: uppercase;
    transition: .2s ease all;

    &:hover {
        background: colour(pink);
        color: colour(white);
    }

    &__solid {
        background: colour(pink);
        color: colour(white);

        &:hover {
            background: colour(white);
            color: colour(pink);
        }
    }

    &__wide {
        display: block;
        text-align: center;
    }

    &__large {
        margin-bottom: 2rem;
        transform: scale(1.4);
    }
}
