/* == Typography == */
h1,
h2,
h3,
h4,
h5,
legend {
    color: colour(jorange);
    font-family: $font-main;
    font-weight: normal;
    margin: 0 0 0.6em;
    word-break: break-word;
}

h1 {
    font-size: clamp-sizes(2.011, 4.209);
    font-weight: 800;
    line-height: 2.5rem;
}

h2 {
    font-size: clamp-sizes(1.749, 3.157);
    font-weight: 700;
}

h3 {
    font-size: clamp-sizes(1.521, 2.369);
    font-weight: 700;
}

h4 {
    font-size: clamp-sizes(1.322, 1.777);
    font-weight: 600;
}

h5 {
    font-size: clamp-sizes(1.15, 1.333);
    font-weight: 600;
}

p {
    font-family: $font-main;
    font-size: 1.1rem;
    font-weight: 400;
    line-height: 1.4rem;
    padding: 0;

    &:not(:last-child) {
        margin: 0 0 1em 0;
    }
}

b,
strong {
    font-weight: 700;
}

i,
em {
    font-style: italic;
}

small {
    font-size: .8rem;
}

figcaption {
    color: colour(grey);
    font-size: .9rem;
    margin-top: .75rem;
}
