header.primary {
    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 3rem 0;
        @include media(md) {
            flex-direction: column;
            gap: 2rem;
        }

        .nav {
            &__logo {
                flex-basis: 204px;
                @include media(md) {
                    flex-basis: 100%;
                }
            }

            &__links {
                display: flex;
                align-items: center;
                gap: .75rem;
                @include media(md) {
                    flex-wrap: wrap;
                    justify-content: center;
                }

                .button {
                    margin: 0;
                }

                .user {
                    background:colour(white);
                    border: 2px solid colour(pink);
                    border-radius: 9999px;
                    padding: .45rem .58rem;
                    transition: .2s ease all;

                    svg {
                        fill: colour(pink);
                        height: 1rem;
                        transition: .2s ease all;
                    }

                    &:hover {
                        background:colour(pink);

                        svg {
                            fill: colour(white);
                        }
                    }
                }
            }
        }
    }
}
