/* == Utilities == */
//-----------------------------------------
// Accessability
//-----------------------------------------
.skip-link {
    @include visually-hidden;
    position: absolute;
    top: 0;
    left: 0;
    padding: 2rem;
    background-color: #fff;

    &:focus {
        @include visually-shown(absolute);
    }
}

//-----------------------------------------
// Hide Elements
//-----------------------------------------
.mobile-only {
    display: none !important;
    @include media(md) {
        display: inherit !important;
    }
}

.desktop-only {
    display: block !important;
    @include media(md) {
        display: none !important;
    }
}

//-----------------------------------------
// Grid System
//-----------------------------------------
.grid-row {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    @include media(md) {
        flex-wrap: wrap;
    }

    @for $i from 1 through 12 {
        .col-#{$i} {
            width: span($i);
            @include media(md) {
                width: span(12);
                &:not(:last-child) {
                    margin: 0 0 1em 0;
                }
            }
        }
    }
}

//-----------------------------------------
// Colours
//-----------------------------------------
@each $colour, $value in $colours {
    .bg-#{$colour} {
        background-color: $value
    }
}

@each $colour, $value in $colours {
    .text-#{$colour} {
        color: $value
    }
}

.accent {
    @include colour-accents;
}


//-----------------------------------------
// Shapes
//-----------------------------------------
.bg {
    &--shaped {
        margin-top: -6rem;
        padding-top: 6rem;
        z-index: 1;
        @include media(md) {
            margin-top: -4rem;
            padding-top: 2rem;
        }

        > img {
            position: absolute;
            inset: 0 0 auto 0;
            width: 100%;
            z-index: -1;
        }
    }
}

//-----------------------------------------
// Text
//-----------------------------------------
.text {
    &--center {
        text-align: center;
    }
}

//-----------------------------------------
// Padding
//-----------------------------------------
.pt {
    &-2 {
        padding: 2rem 0;
    }
}
