.bundle {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.5rem .75rem;
    margin-bottom: 2rem;
    @include media(md) {
        grid-template-columns: repeat(2, 1fr);
    }

    &__links {
        text-align: center;
    }

    &__headline {
        border-bottom: 2px solid colour(pink);
        border-top: 2px solid colour(pink);
        margin: 1.5rem 0 0;
        padding: 0.5rem 0;
        grid-area: 1/1/1/5;
        @include media(md) {
            grid-area: 1/1/1/3;
        }

        h3 {
            margin: 0;
        }
    }

    &__game {
        aspect-ratio: 197 / 92;

        img {
            height: 100%;
            object-fit: cover;
            width: 100%;
        }
    }
}
