html {
    min-height: 100%;
    font-size: 16px; // This establishes what 1rem is
    text-size-adjust: 100%;
}

body {
    background: colour(white);
    background-size: cover;
    color: $plain-text;
    font-family: $font-main;
    min-width: 320px;
    overflow-x: hidden;
    position: relative;
    text-rendering: optimizeSpeed;
    width: 100%;
}

a {
    position: relative;
    color: $link;
    font-weight: 600;
    transition: 0.3s ease all;
    &:hover {
        color: $link-hover;
    }
}

button {
    @extend a;
}

section {
    position: relative;
    margin: 2rem 0;

    @include media(md) {
        margin: 2rem 0;
    }
}

.container {
    @include container(1120px);
    position: unset;
}

.embed-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;

    iframe,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

//------------------------------------
// Focus States
//------------------------------------
%dashed-focus {
    outline: 0.2rem dashed colour(coral);
    outline-offset: 0.2rem;
}

::selection {
    color: white;
    -webkit-text-fill-color: white;
    background-color: colour(black);
}
