/* == Mixins == */

//-----------------------------------------
// Media query mixin
//-----------------------------------------
@mixin media($bp, $rule: max-width) {
    @media screen and (#{$rule}: map-get($breakpoints, $bp)) {
        @content;
    }
}

//-----------------------------------------
// Container mixin
//-----------------------------------------
@mixin container($max-width: 1320px, $padding: 36px) {
    margin-left: auto;
    margin-right: auto;
    max-width: $max-width;
    padding-left: $padding;
    padding-right: $padding;
    position: relative;

    @include media(sm) {
        padding-left: $padding;
        padding-right: $padding;
    }
}

//-----------------------------------------
// Absolute center content
//-----------------------------------------
@mixin center-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

//-----------------------------------------
// Line-height Cropping
//-----------------------------------------
@mixin line-height-crop($line-height) {
    &::before {
        content: '';
        display: block;
        height: 0;
        width: 0;
        margin-top: calc((1 - #{$line-height}) * 0.5em);
    }
}

//-----------------------------------------
// Use Aspect Ratio
//-----------------------------------------
@mixin aspect-ratio($width: 16, $height: 9) {
    position: relative;
    &:before {
        display: block;
        content: '';
        width: 100%;
        padding-top: ($height / $width) * 100%;
    }

    > * {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
}

//-----------------------------------------
// Colour Accents
//-----------------------------------------
@mixin colour-accents {
    @each $name, $hex in $colours {
        &--#{$name} {
            color: $hex;
        }
    }
}

//-----------------------------------------
// Accessibility Helpers
//-----------------------------------------
@mixin visually-hidden() {
    position: absolute !important;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    border: 0;
}

@mixin visually-shown($position: inherit) {
    position: $position !important;
    width: auto;
    height: auto;
    margin: 0;
    overflow: auto;
    clip: auto;
}

//-----------------------------------------
// Button Hover States
//-----------------------------------------
@mixin hover-slide(
    $colour: colour(coral),
    $width: 14rem,
    $text: colour(white)
) {
    &:hover {
        color: $text;
        box-shadow: inset $width 0 0 0 $colour;
    }
}

@mixin hover-split($colour: colour(coral), $bg: colour(white), $size: .3rem) {
    background: $bg;
    &:hover {
        transform: translate(-2px,-2px);
        box-shadow: $size $size 0 0 $colour,
    }
}
