.charities {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    @include media(md) {
        grid-template-columns: repeat(2, 1fr);
    }

    .charity {
        aspect-ratio: 1/1;
        background: colour(pink);
        border: 2px solid colour(pink);

        &__image {
            aspect-ratio: 5 / 4;
            overflow: hidden;

            img {
                object-fit: cover;
                height: 100%;
                width: 100%;
            }
        }

        &__total {
            color: colour(white);
            font-size: 1.2rem;
            padding: 0.25rem;

            span {
                display: block;
                font-size: 1.75rem;
                font-weight: 600;
                margin-bottom: 0.25rem;
            }
        }
    }
}
